const { Player } = TextAliveApp;

let songUrl = 'https://www.youtube.com/watch?v=a-Nf3QUFkOU';
let isAnimation = false;
let lyricDivArray = [];

// ÂçòË™û„Åî„Å®„Å´Ê≠åË©û„ÇíË°®Á§∫
const animateWord = (now, unit) => {
  if (unit.contains(now)) {
    $('#text').html(unit.text);
  }
};

const run = () =>{
  //ÂàùÊúüÂåñÂá¶ÁêÜ
  $('#info').empty();
  $('#media').empty();

  const player = new Player({ app: true , mediaElement: document.querySelector('#media')});
  console.log('Run');

  let c;
  let lyricId = 1;
  player.addListener({
    onAppReady: (app) => {
      console.log('AppReady');
      player.createFromSongUrl(songUrl);
    },

    // Ê•ΩÊõ≤ÊÉÖÂ†±Ë™≠„ÅøËæº„ÅøÂÆå‰∫ÜÂæå„ÄÅÂëº„Å∞„Çå„Çã
    // „Åì„ÅÆÊôÇÁÇπ„Åß„ÅØrequestPlay()Á≠â„ÅåÂÆüË°å‰∏çËÉΩ
    onVideoReady: (v) => {
      console.log('VideoReady');
      let infoContents = '';
      infoContents += '<h1>Ê•ΩÊõ≤ÊÉÖÂ†±</h1>';
      infoContents += '<h2>Ê•ΩÊõ≤ÂêçÔºö<br>' + player.data.song.name + '</h2>';
      infoContents += '<h2>„Ç¢„Éº„ÉÜ„Ç£„Çπ„ÉàÂêçÔºö<br>' + player.data.song.artist.name + '</h2>';
      $('#info').html(infoContents);
      $('#text').html('[ÂÜçÁîüÊ∫ñÂÇôÂæÖÊ©ü‰∏≠]');
    },

    // ÂÜçÁîüÊ∫ñÂÇôÂÆå‰∫ÜÂæå„ÄÅÂëº„Å∞„Çå„Çã
    // „Åì„Çå‰ª•Èôç„ÄÅrequestPlay()Á≠â„ÅåÂÆüË°åÂèØËÉΩ
    onTimerReady: () => {
      console.log('TimerReady');
      $('#text').html('');
      player.requestPlay();
      // ÂÆöÊúüÁöÑ„Å´Âëº„Å∞„Çå„ÇãÂêÑÂçòË™û„ÅÆ "animate" Èñ¢Êï∞„Çí„Çª„ÉÉ„Éà„Åô„Çã
      /*let w = player.video.firstPhrase;
      while (w) {
        w.animate = animateWord;
        w = w.next;
      }*/
    },

    onTimeUpdate: (position) => {
      // Ê≠åË©ûÊÉÖÂ†±„Åå„Å™„Åë„Çå„Å∞„Åì„Çå„ÅßÂá¶ÁêÜ„ÇíÁµÇ„Çè„Çã
      if (!player.video.firstChar) {
        return;
      }
  
      // 500msÂÖà„Åã„ÇâÂßã„Åæ„ÇãÊñáÂ≠óÔΩû„Éï„É¨„Éº„Ç∫„ÅÆÊúÄÂæå„Åæ„ÅßÂèñÂæó
      let current = c || player.video.firstPhrase;
      let fontSize = 50;
      while (current && current.startTime < position + 500) {
        if (c !== current) {
          const div = document.createElement("div");
          lyricDivArray.push(div);
          div.id = "lyric_" + lyricId;
          lyricId++;
          div.style.position = 'absolute';
          div.style.top = 510 + "px";
            let str = current.text;
            let charArray = str.split('');
            for(var i = 0; i < charArray.length; ++i){
              const span = document.createElement("span");
              span.style.fontSize = fontSize + "px";
              span.innerHTML = charArray[i];
              div.appendChild(span);
              fontSize -= 1;
            }
            $('#text').append(div);
          c = current;
        }
        if(!isAnimation){
          isAnimation = true;
          setAnimation();
        }
        current = current.next;
      }
    },
  
  });
}

const setAnimation = () =>{
  let delay = 1000 / 50; // 1 Áßí„Åß 50 „Éï„É¨„Éº„É†
  let timer = setInterval(function() {
  //500ms
  let move = 8;
  lyricDivArray.forEach((item, index) => {
    let currentPos = parseInt(item.style.top);
    let newPos = (currentPos - move);
    item.style.top = newPos + "px";
  });
}, delay)
};

window.run = run